import { ServiceProvider } from './../../providers/service/service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the DataInformasiPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-data-informasi',
  templateUrl: 'data-informasi.html',
})
export class DataInformasiPage {

  informasi;

  constructor(public navCtrl: NavController, public navParams: NavParams, private service: ServiceProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DataInformasiPage');
  }

  ionViewWillEnter() {
    if(!this.navParams.get('admin')) {
      this.navCtrl.setRoot('BerandaPage')
    } else {
      this.service.retrieveInformationData().then((res:any) => {
        console.log('data informasi', res);
        this.informasi = res
      })
    }
  }

  clickInput(info) {
    console.log(info)
    this.navCtrl.push('DetailInformasiPage', {data: info});
  }

  newInformation() {
    console.log('new information')
  }
 
}



